.orders{
   &__container{
      grid-area: content;
      padding-left:41px;
      padding-right: 39px;
      margin-bottom: 50px;
      >h1{
         margin-left: 68px;
      }
   }
   
   &__header{
      display: flex;
      align-items: flex-end;
      margin-top: 30px;
      margin-bottom: 30px;
      margin-left: 10px;
      &-title{
         font-family: 'Raleway';
         font-style: normal;
         font-weight: 700;
         font-size: 20px;
         line-height: 23px;
      }
   }
   &__list{
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      
      &-item{
         height: 236px;
         width: 446px;
         background: #FFFFFF;
         border: 1px solid #AEC2EA;
         box-sizing: border-box;
         border-radius: 32px;
         margin: 0 10px 20px 10px;
         padding: 20px;
         
      }
      &-header{
         display: flex;
         align-items: flex-end;
         
         &-date{
            font-family: 'Raleway';
            font-style: normal;
            font-weight: 600;
            font-size: 14px;
            line-height: 140%;
            color: #727280;
         }
         &-detailed{
            font-family: 'Raleway';
            font-style: normal;
            font-weight: 600;
            font-size: 13px;
            line-height: 127.7%;
            color: #2967FF;
            margin-left: 10px;
         }
      }
      &-row{
         display: flex;
         justify-content: flex-start;
         margin-top: 10px;
      }
      &-col{
         margin-right: 20px;
         p{
            font-family: 'Raleway';
            font-style: normal;
            font-weight: 500;
            font-size: 13px;
            line-height: 122%;
            color: #727280;
            margin-bottom: 3px;
         }
         h1{
            font-family: 'Raleway';
            font-style: normal;
            font-weight: 600;
            font-size: 13px;
            line-height: 122%;
            color: #2D2D2F;
         }
      }

      
   }
  
  
   
}