.basket{
   &__container{
      grid-area: content;
      padding-left:51px;
      padding-right: 49px;
      margin-bottom: 50px;
   }
   
   &__header{
      display: flex;
      align-items: flex-end;
      margin-top: 30px;
      margin-bottom: 30px;
      &-title{
         font-family: Raleway;
         font-style: normal;
         font-weight: bold;
         font-size: 20px;
         line-height: 23px;
         font-feature-settings: 'pnum'
      }
      &-clear{
         margin-left: 30px;
         font-family: 'Raleway';
         font-style: normal;
         font-weight: 600;
         font-size: 16px;
         line-height: 19px;
         font-feature-settings: 'pnum' on, 'lnum' on;
         color: #FF2D87;
         &:hover{
            cursor: pointer;
            color: #752345;
         }
      }
   } 
   &__list{
      border: 1px solid #AEC2EA;
      box-sizing: border-box;
      border-radius: 20px;
      padding-bottom: 30px;
      h1{
         margin-left: 58px;
      }
      &-header{
         border: 1px solid #AEC2EA;
         box-sizing: border-box;
         border-radius: 20px;
         height: 100px;
         display: flex;
         align-items: center;
         justify-content: space-between;
         margin-bottom: 40px;
         &-blok{
            margin-left: 58px;
            font-family: 'Raleway';
            font-style: normal;
            font-weight: 600;
            font-size: 20px;
            line-height: 130%;
         }
         &-total{
            p{
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 400;
               font-size: 16px;
               line-height: 140%;
            }
            h2{
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 700;
               font-size: 20px;
               line-height: 23px;
            }
         }
         &-button{
            width: 214px;
            height: 50px;
            background: #2967FF;
            box-sizing: border-box;
            border-radius: 51px;
            display: flex;
            align-items: center;
            justify-content: center;
            p{
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 600;
               font-size: 16px;
               line-height: 19px;
               color:#f5f6f8; 
            }
            &:hover{
               background: #f5f6f8;
               cursor: pointer;
               border: 1px solid #2967FF;
               p{
                  color: #2967FF;
               }
            }
         }
         &-images{
            height: 100%;
            width: 160px;
            background-image: url("img/Group.png");
            margin-right: 20px;
         }
      }
      &-item{
         padding-top: 20px;
         border-bottom: 1px solid #AEC2EA;
         box-sizing: border-box;
         margin-left: 58px;
         margin-right: 78px;
         height: 98px;
         display: flex;
         align-items: flex-start;
         justify-content: space-between;
        
         &-blok{
            width: 60px;
            height: 63px;
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center center;
         }
         &-title{
            margin-left: 20px;
            p{
               width: 292px;
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 400;
               font-size: 16px;
               line-height: 140%;
            }
            h2{
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 700;
               font-size: 20px;
               line-height: 23px;
            }
         }
         &-button{
            width: 135px;
            height: 44px;
            box-sizing: border-box;
            border-radius: 35px;
            display: flex;
            align-items: center;
            justify-content: space-around;
            border: 1px solid #AEC2EA;
            p{
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 400;
               font-size: 16px;
               line-height: 140%;
               text-align: center;
               color: #2D2D2F;
            }
            &-count{
               &:hover{
                  font-weight: 700;
                  cursor: pointer;
                  
                                          
               }
            }
            
         }
         &-price{
            margin-left: 20px;
            p{
               
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 400;
               font-size: 16px;
               line-height: 122%;
               text-decoration: line-through;
               color: #8D8D8E;
            }
            h2{
               font-family: 'Raleway';
               font-style: normal;
               font-weight: 600;
               font-size: 16px;
               line-height: 140%;
               
            }
         }


         
         &-clear{
            height: 20px;
            width: 20px;
            background-image: url("img/delete.svg");
            &:hover{
               cursor: pointer;
               color: #121213;
               background-color: #9292963b;
            }
         }
      }
   }
  
   
}