.delivery{
   &__container{
      grid-area: content;
      padding-left:51px;
      padding-right: 49px;
      margin-bottom: 50px;
      h1{
         margin-left: 58px;
      }
   }
   
   &__header{
      display: flex;
      align-items: flex-end;
      margin-top: 30px;
      margin-bottom: 30px;
      &-title{
         font-family: 'Raleway';
         font-style: normal;
         font-weight: 700;
         font-size: 20px;
         line-height: 23px;
      }
   }
   &__list{
      display: flex;
      flex-wrap: wrap;
      
      &-colum{
         label{
            font-family: 'Raleway';
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 140%;
         }
         &-blok{
            margin-top: 10px;
            margin-bottom: 30px;
         }
         &-input{
            height: 40px;
            width: 292px;
            border: 1px solid #F0F4FB;
            box-sizing: border-box;
            border-radius: 45px;
            padding-left: 10px;
         }
         &-left{
            margin-left:78px ;
            &-blok{
               width: 252px;
               height: 82px;
               background: #F0F4FB;
               border-radius: 20px;
               padding: 20px;
               margin-bottom: 20px;
               &-item{
                  display: flex;
                  justify-content: space-between;
                  height: 10px;
                  margin-bottom: 10px;
                  p{
                     font-family: 'Raleway';
                     font-style: normal;
                     font-weight: 400;
                     font-size: 14px;
                     line-height: 140%;
                     color: #727280;
                  }
                  h1{
                     font-family: 'Raleway';
                     font-style: normal;
                     font-weight: 400;
                     font-size: 16px;
                     line-height: 140%;
                     text-align: right;
                     color: #727280;
                  }
               }
            }
            &-button{
               width: 292px;
               height: 50px;
               background: #2967FF;
               border-radius: 51px;
               display: flex;
               justify-content: center;
               align-items: center;
               p{
                  font-family: 'Raleway';
                  font-style: normal;
                  font-weight: 600;
                  font-size: 16px;
                  line-height: 19px;
                  color: #FFFFFF;
               }
               &:hover{
                  background: #f5f6f8;
                  cursor: pointer;
                  border: 1px solid #2967FF;
                  p{
                     color: #2967FF;
                  }
               }
            }
         }
      }
   }
  
  
   
}